<!--App首页-->
<template>
  <div id="app">
    <router-view v-on:chosePersons="getChosePerson" v-bind:chosePersons="chosePersons"></router-view>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data: function () {
      return {
        chosePersons: []
      }
    },
    methods:{
      getChosePerson: function (persons) {
        this.chosePersons = persons
      }
    }

  }
</script>




